@font-face {
  font-family: "Plus Jakarta Sans";
  src: url("/assets/fonts/PlusJakartaSans-VariableFont_wght.ttf") format("truetype") tech("variations"), url("/assets/fonts/PlusJakartaSans-VariableFont_wght.ttf") format("truetype-variations");
  font-weight: 500 700;
}
*, *::before, *::after {
  box-sizing: border-box;
}

* {
  padding: 0;
  margin: 0;
}

html {
  scroll-behavior: smooth;
  overflow-x: hidden;
}

body {
  min-height: 100vh;
}

img, svg, picture, video {
  display: block;
  max-width: 100%;
}

input, button, textarea, select {
  font: inherit;
}

h1, h2, h3 {
  color: rgb(3, 3, 3);
  line-height: 1.25;
}

h1, .fs-l {
  font-size: 2.25rem;
}
@media (min-width: 45rem) {
  h1, .fs-l {
    font-size: 2.75rem;
  }
}
@media (min-width: 75rem) {
  h1, .fs-l {
    font-size: 3.5rem;
  }
}

h2, .fs-m {
  font-size: 1.625rem;
}
@media (min-width: 45rem) {
  h2, .fs-m {
    font-size: 2rem;
  }
}
@media (min-width: 75rem) {
  h2, .fs-m {
    font-size: 2.5rem;
  }
}

h3, .fs-s {
  font-size: 1.5rem;
}
@media (min-width: 45rem) {
  h3, .fs-s {
    font-size: 2rem;
  }
}

body {
  font-family: "Plus Jakarta Sans", sans-serif;
  color: rgb(122, 116, 110);
  line-height: 1.5;
  font-size: 1rem;
}
@media (min-width: 45rem) {
  body {
    font-size: 1.125rem;
  }
}

.container {
  width: calc(100% - 2rem);
  max-width: 69.4375rem;
  margin-inline: auto;
}
@media (min-width: 45rem) {
  .container {
    width: calc(100% - 5rem);
  }
}

.btn__cta {
  display: inline-block;
  padding: 0.5rem 1.75rem;
  border-radius: 1.75rem;
  font-weight: 700;
  text-decoration: none;
  line-height: 2;
  color: rgb(255, 247, 240);
  transition: all 250ms ease-in-out;
}
.btn__cta-dark {
  background-color: rgb(3, 3, 3);
}
.btn__cta-dark:hover {
  background-color: rgb(117, 92, 222);
}
.btn__cta-light {
  background-color: rgb(235, 117, 101);
}
.btn__cta-light:hover {
  background-color: rgb(246, 165, 96);
}

.brand {
  display: inline-block;
  width: 3rem;
  height: 3rem;
}
@media (min-width: 45rem) {
  .brand {
    width: 4rem;
    height: 4rem;
  }
}

.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.5rem;
  grid-template-areas: "gd gd" "uiux apps" "illt illt" "photo photo" "motion motion";
}
@media (min-width: 45rem) {
  .grid {
    grid-template-columns: repeat(4, 1fr);
    grid-template-areas: "gd gd uiux apps" "gd gd illt illt" "photo photo motion motion";
  }
}
@media (min-width: 75rem) {
  .grid {
    grid-template-columns: repeat(6, 1fr);
    grid-template-areas: "gd gd uiux apps photo photo" "gd gd illt illt motion motion";
  }
}
.grid__item {
  padding: 1.5rem;
  border-radius: 0.5rem;
  color: rgb(255, 247, 240);
  font-weight: 700;
  font-size: 1.5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: start;
}
.grid__item:nth-of-type(1) {
  background-color: rgb(117, 92, 222);
  gap: 5.875rem;
  grid-area: gd;
}
.grid__item:nth-of-type(2) {
  background-color: rgb(246, 165, 96);
  gap: 2.5rem;
  grid-area: uiux;
}
.grid__item:nth-of-type(3) {
  background-color: rgb(243, 158, 158);
  gap: 2.5rem;
  grid-area: apps;
}
.grid__item:nth-of-type(4) {
  background-color: rgb(235, 117, 101);
  gap: 1rem;
  grid-area: illt;
}
.grid__item:nth-of-type(5) {
  background-color: rgb(97, 196, 183);
  gap: 2.5rem;
  grid-area: photo;
}
.grid__item:nth-of-type(6) {
  background-color: rgb(85, 32, 73);
  gap: 2.5rem;
  grid-area: motion;
}
.grid__img {
  align-self: end;
}

.header {
  padding-top: 1rem;
  margin-bottom: 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (min-width: 45rem) {
  .header {
    padding-top: 2.125rem;
    margin-bottom: 4.0625rem;
  }
}

body {
  background-color: rgb(255, 247, 240);
}

.hero {
  text-align: center;
  margin-bottom: 6.25rem;
}
@media (min-width: 45rem) {
  .hero {
    margin-bottom: 7.5rem;
  }
}
@media (min-width: 75rem) {
  .hero {
    margin-bottom: 8.5rem;
  }
}
.hero__text {
  margin-bottom: 1.625rem;
}
@media (min-width: 45rem) {
  .hero__text {
    margin-bottom: 4rem;
  }
}
@media (min-width: 75rem) {
  .hero__text {
    margin-bottom: 5rem;
  }
}
.hero__title {
  margin-bottom: 1rem;
}
@media (min-width: 45rem) {
  .hero__title {
    margin-bottom: 1.6875rem;
  }
}
@media (min-width: 75rem) {
  .hero__title {
    margin-bottom: 1.4375rem;
  }
}
.hero__description {
  max-width: 45.625rem;
  margin-inline: auto;
}

.about {
  text-align: center;
  margin-bottom: 6.25rem;
}
@media (min-width: 45rem) {
  .about {
    text-align: start;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: 1fr;
    gap: 0.625rem;
    align-items: center;
    margin-bottom: 7.5rem;
  }
}
@media (min-width: 75rem) {
  .about {
    margin-bottom: 5.5rem;
    gap: 1.875rem;
  }
}
@media (min-width: 45rem) {
  .about__text {
    grid-column: 7/13;
  }
}
.about__img {
  margin-bottom: 2.5rem;
}
@media (min-width: 45rem) {
  .about__img {
    margin-bottom: auto;
    grid-column: 1/7;
    margin-left: -5rem;
  }
}
@media (min-width: 75rem) {
  .about__img {
    margin-left: auto;
    grid-column: 1/6;
  }
}
.about__description {
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
}
@media (min-width: 75rem) {
  .about__description {
    margin-top: 2.0625rem;
    margin-bottom: 2.0625rem;
  }
}